<template>
	<view>
		<view class="city">
			<picker mode="selector" :range="arealist" range-key='name' :value="index"
				@change='changeCity($event,arealist[index].id)'>
				<view>{{city?city:arealist.length!=0?arealist[index].name:'市'}}
					<image src="../../static/img/xiala.png" mode="widthFix"></image>
				</view>
			</picker>
			<picker mode="selector" :range="itemslist" range-key="name" :value="index1"
				@change="changeCountry($event,itemslist[index1].id)">
				<view>{{country?country:itemslist.length!=0?itemslist[index1].name:'區'}}
					<image src="../../static/img/xiala.png" mode="widthFix"></image>
				</view>
			</picker>
		</view>
	</view>
</template>

<script>
	export default {
		name: "region",
		props: {
			arealist: {
				type: Array
			},
			city: {
				type: String,
				default: ''
			},
			country: {
				type: String,
				default: ''
			},
			index1: {
				type: Number,
				default: 0
			},
			index: {
				type: Number,
				default: 0
			},
			itemslist: {
				type: Array
			}
		},
		data() {
			return {

			};
		},
		methods: {
			changeCountry(e, id) {
				let data = {
					index: e.target.value,
					id: id
				}
				this.$emit('changeCountry', data)

			},
			changeCity(e, id) {
				this.$emit('changeCity', {
					index: e.target.value,
					id: id
				})
			},

		}
	}
</script>

<style lang="less">
	picker {
		border: 1px solid #A3A3A3;
		padding: 20rpx 38rpx;
		border-radius: 15rpx;
		display: inline-block;
		margin: 0 10rpx 10rpx 0;
		background-color: #fff;

		image {
			margin-left: 10rpx;
			width: 40rpx;
			height: 40rpx;
			vertical-align: middle;
		}
	}
</style>
